<template>
    <el-breadcrumb class="breadcrumb" separator="/">
        <el-breadcrumb-item :to="{ path: '/index' }" class="first-bar">首页</el-breadcrumb-item>
        <el-breadcrumb-item v-for="(item,index) in items" :key="index">{{item}}</el-breadcrumb-item>
    </el-breadcrumb>
</template>
<script>
export default {
  name: "breadcrumb",
  data() {
    return {
      items: {}
    };
  },
  computed: {
    menu() {
      return this.$store.getters.menu;
    }
  },
  created() {
    this.showMenuList();
  },
  methods: {
    showMenuList() {
      let matched = this.$route.path;
      const data = this.menu;
      const tData = [];
      for (let key = 0; key < data.length; key++) {
        if (data[key].children[0].path == matched) {
          const item = data[key].name;
          const item2 = data[key].children[0].name;
          tData.push(item, item2);
        } else if (data[key].children[1].path == matched) {
          const item = data[key].name;
          const item2 = data[key].children[1].name;
          tData.push(item, item2);
        }
      }
      this.items = tData;
    }
  }
};
</script>
<style scoped>
.breadcrumb {
  height: 50px;
  line-height: 50px;
  background-color: #eff2f7;
  margin-bottom: 5px;
  width: 100%;
}
.first-bar {
  margin-left: 10px;
}
</style>
