<template>
    <el-row class="menu_page">
         <el-col>
             <el-menu
                mode="vertical"
                background-color="#324057"
                text-color="#fff"
                active-text-color="#409eff" 
                class="el-menu-vertical-demo">
                <router-link to="/home">
                    <el-menu-item index="0">
                        <i class="el-icon-menu"></i>
                        <span slot="title">首页</span>
                    </el-menu-item>
                </router-link>
                <template  v-for="item in items" >
                    <el-submenu v-if="item.children" :index="item.path" :key="item.path">
                        <template slot="title">
                            <i :class="item.icon"></i>
                            <span slot="title">{{item.name}}</span>
                        </template>
                        <router-link v-for="(citem,cindex) in item.children" :to="citem.path" :key="cindex">
                            <el-menu-item 
                                :index='citem.path'>
                                <span slot="title">{{citem.name}}</span>
                            </el-menu-item> 
                        </router-link>
                    </el-submenu>   
                </template>
             </el-menu>
         </el-col>
    </el-row>
</template>
<script>
export default {
  name: "leftmenu",
  data() {
    return {
      items: []
    };
  },
  computed: {
    user() {
      return this.$store.getters.user;
    }
  },
  created() {
    this.showMenuList();
  },
  methods: {
    showMenuList() {
      if (this.user.identity == "manager") {
        this.items.push(
          {
            icon: "el-icon-menu",
            name: "组件",
            path: "component",
            children: [
              { path: "/layoutcomponent", name: "Layout布局" },
              { path: "/layoutcompon", name: "Layout布局" },
              { path: "/layoutc", name: "Layout布局" }
            ]
          },
          {
            icon: "el-icon-goods",
            name: "资金管理",
            path: "fund",
            children: [
              { path: "/fundlist", name: "资金流水" },
              { path: "/funddata", name: "资金数据" }
            ]
          },
          {
            icon: "el-icon-setting",
            name: "信息管理",
            path: "info",
            children: [
              { path: "/adminlist", name: "用户信息" },
              { path: "/infoshow", name: "个人信息" }
            ]
          }
        );
        this.$store.dispatch("setMenu", this.items);
      } else {
        this.items.push(
          {
            icon: "el-icon-goods",
            name: "资金管理",
            path: "fund",
            children: [
              { path: "/fundlist", name: "资金流水" },
              { path: "/funddata", name: "资金数据" }
            ]
          },
          {
            icon: "el-icon-setting",
            name: "信息管理",
            path: "info",
            children: [
              { path: "/adminlist", name: "用户信息" },
              { path: "/infoshow", name: "个人信息" }
            ]
          }
        );
        this.$store.dispatch("setMenu", this.items);
      }
    }
  }
};
</script>
<style scoped>
.menu_page {
  position: fixed;
  top: 71px;
  left: 0;
  min-height: 100%;
  background-color: #324057;
  z-index: 99;
}
.el-menu {
  border: none;
}
.fa-margin {
  margin-right: 5px;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 180px;
  min-height: 400px;
}
.el-menu-vertical-demo {
  width: 35px;
}
.el-submenu .el-menu-item {
  min-width: 180px;
}

.hiddenDropdown,
.hiddenDropname {
  display: none;
}
a {
  text-decoration: none;
}
</style>
