<template>          
    <div>
      <Breadcrumb></Breadcrumb>
        <div id="mapContainer" style="width:1200px;height:770px;margin:20px auto 0;"></div>
    </div>
</template>
        
<script>
import echarts from "echarts/lib/echarts";
import option from "../echarts/map-option"; //引入配置项
import "echarts/map/js/china.js"; //引入china.js地图文件
import Breadcrumb from "../components/Breadcrumb";

export default {
  name: "funddata",
  data() {
    return {};
  },
  components: {
    Breadcrumb
  },
  methods: {
    drawChinaMap() {
      var myChart = this.$echarts.init(document.getElementById("mapContainer"));
      myChart.setOption(option);
    }
  },
  mounted() {
    this.drawChinaMap();
  }
};
</script>
        
