<template>
    <div class="login">
            <vue-particles
        color="#409eff"
        :particleOpacity="0.7"
        :particlesNumber="100"
        shapeType="star"
        :particleSize="4"
        linesColor="#dedede"
        :linesWidth="1"
        :lineLinked="true"
        :lineOpacity="0.4"
        :linesDistance="150"
        :moveSpeed="3"
        :hoverEffect="true"
        hoverMode="grab"
        :clickEffect="true"
        clickMode="repulse"
      >
      </vue-particles>
        <section class="form_container">
            <div class="manage_tip">
                <span class="title">XX在线管理系统</span>
                <el-form :model="loginUser" :rules="rules" ref="loginForm" label-width="60px" class="loginForm">

                    <el-form-item label="邮箱" prop="email">
                        <el-input v-model="loginUser.email" placeholder="请输入邮箱"></el-input>
                    </el-form-item>

                    <el-form-item label="密码" prop="password">
                        <el-input type="password" v-model="loginUser.password" placeholder="请输入密码"></el-input>
                    </el-form-item>

                    <el-form-item>
                        <el-button type="primary" class="submit_btn" @click="submitForm('loginForm')">登录</el-button>
                    </el-form-item>
                    <div class="tiparea">
                        <p>温馨提示：</p>
                        <p class="tipareatip">未登录过的新用户，自动注册</p>
                        <p class="tipareatip">注册过的用户可凭账号密码登录</p>
                    </div>
                </el-form>
            </div>
        </section>
        <remote-js src="http://pv.sohu.com/cityjson?ie=utf-8"></remote-js>
    </div>
</template>

<script>
export default {
  name: "login",
  components: {},
  data() {
    return {
      loginUser: {
        email: "",
        password: ""
      },
      registerUser: {
        name: "",
        email: "",
        password: "",
        identity: "employee",
        ip: "",
        city: "",
        id: Math.random()
          .toString(16)
          .slice(2, 8),
        //随机生成6位id
        date: Date.now()
      },
      sessionUser: {
        id: "",
        email: "",
        identity: ""
      },
      rules: {
        email: [
          {
            type: "email",
            required: true,
            message: "邮箱格式不正确",
            trigger: "blur"
          }
        ],
        password: [
          {
            required: true,
            message: "密码不能为空",
            trigger: "blur"
          },
          {
            min: 6,
            max: 20,
            message: "长度在6-20之间",
            trigger: "blur"
          }
        ]
      }
    };
  },
  created() {},
  components: {
    //引入查询ip与地址的js
    "remote-js": {
      render(createElement) {
        return createElement("script", {
          attrs: { type: "text/javascript", src: this.src }
        });
      },
      props: {
        src: { type: String, required: true }
      }
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          //这是无node接口的版本，使用野狗云的实时通信引擎
          this.$axios.get("/users.json").then(res => {
            const data = res.data;
            const users = [];
            for (let key in data) {
              const user = data[key];
              users.push(user);
            }
            //判断有无用户存在
            let result = users.filter(user => {
              return (
                user.email === this.loginUser.email &&
                user.password === this.loginUser.password
              );
            });
            if (result != null && result.length > 0) {
              //登录成功存储token,这是没有node接口的版本，存储到sessionStorage
              this.sessionUser.id = result[0].id;
              this.sessionUser.email = result[0].email;
              this.sessionUser.identity = result[0].identity;
              sessionStorage.setItem(
                "eleToken",
                JSON.stringify(this.sessionUser)
              );
              //token存储到xuex，同上
              this.$store.dispatch("setUser", this.sessionUser);
              this.$router.push("/index");
            } else {
              //登录失败创建账户
              this.registerUser.ip = returnCitySN["cip"]; //IP地址
              this.registerUser.city = returnCitySN["cname"]; //所在地
              this.registerUser.email = this.loginUser.email;
              this.registerUser.name = this.loginUser.email;
              this.registerUser.password = this.loginUser.password;
              this.$axios.post("users.json", this.registerUser).then(res => {
                this.$message({
                  message: "账号注册成功！",
                  type: "success"
                });
                this.sessionUser.id = this.registerUser.id;
                this.sessionUser.email = this.registerUser.email;
                this.sessionUser.identity = this.registerUser.identity;
                sessionStorage.setItem(
                  "eleToken",
                  JSON.stringify(this.sessionUser)
                );
                //token存储到xuex，同上
                this.$store.dispatch("setUser", this.sessionUser);
                this.$router.push("/index");
              });
            }
          });
        }
      });
    }
  }
};
</script>

<style scoped>
body {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.login {
  position: relative;
  width: 100%;
  height: 100%;
  background: url(../assets/bg.jpg) no-repeat center center;
  background-size: 100% 100%;
}
#particles-js {
  height: 98%;
  margin: 0;
  padding: 0;
}
.form_container {
  width: 370px;
  height: 210px;
  position: absolute;
  top: 20%;
  left: 39%;
  padding: 25px;
  border-radius: 5px;
  text-align: center;
}
.form_container .manage_tip .title {
  font-family: "Microsoft YaHei";
  font-weight: bold;
  font-size: 26px;
  color: #fff;
}
.loginForm {
  margin-top: 20px;
  background-color: #fff;
  padding: 20px 40px 20px 20px;
  border-radius: 5px;
  box-shadow: 0px 5px 10px #cccc;
}

.submit_btn {
  width: 100%;
}
.tiparea {
  text-align: left;
  font-size: 12px;
  color: #333;
}
.tiparea p {
  color: #409eff;
  line-height: 12px;
  padding: 5px;
}
.tiparea .tipareatip {
  padding-left: 60px;
}
</style>
